* {
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  width: 100vw;
  height: 100vh;
  font-family: 'Bauhaus 93', sans-serif;
  font-size: 1.6rem;
  background-image: url('../images/backgr.jpg');
  background-position: 0 50%;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  overflow: hidden;
}

main {
  margin: 0 auto;
  padding: 2rem;
}

#questions {
  position: absolute;
  bottom: 0;
  margin-left: 50px;
  width: 300px;
  margin-bottom: 10px;
}

#questions h2 {
  font-size: 14px;
}

label {
  font-size: 40px;
}

input[type='text'],
input[type='email'],
input[type='number'] {
  font-size: 1.6rem;
  padding: 1rem;
  margin-bottom: 1rem;
  border: none;
  border-radius: 15px;
  background-color: transparent;
  border: 2px solid #292929;
}

button {
  margin-right: 120px;
  padding: 4px;
  background-color: #ce1110;
  color: #fff;
  border: none;
  border-radius: 15px;
  cursor: pointer;

  &:hover {
    background-color: #ce1110;
  }
}

#prizes {
  margin-top: 6%;
  margin-bottom: 4rem;
}

.boxes {
  display: flex;
  padding: 0 20px 0 20px;
  justify-content: space-around;
}

.box {
  width: 200px;
  height: 200px;
  background-image: url('../images/closed.png');
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
  transition: transform 0.3s ease;

  &:hover {
    transform: scale(1.1);
  }

  &.opened {
    background-image: url('../images/opened.png');
  }
}

.smears {
  width: 1000px;
  position: absolute;
  right: -120px;
  bottom: -120px;
}

.conditions,
.won {
  width: 400px;
  position: absolute;
  bottom: 50px;
  right: 160px;
  color: #292929;
  text-align: center;
}

.input-thumb {
  display: flex;
  gap: 30px;
  margin-bottom: 20px;
  text-align: center;
}

@font-face {
  font-family: 'Bauhaus 93';
  src: url('../fonts/Bauhaus\ 93\ Regular.ttf');
}

.dyson {
  position: absolute;
  right: -100%;
  top: 10%;

  &.show {
    animation: slide-in 2s forwards;
  }
}

@keyframes slide-in {
  100% {
    right: 9%;
  }
}

.hidden {
  opacity: 0;
  transition: opacity 1s;
  transition-delay: 1s;
}

.won-text {
  color: #ce1110;
  margin-bottom: 100px;
}
